import { css, createGlobalStyle } from 'styled-components';
import reset from 'styled-reset';

import NES_PALETTE from '../lib/nesPalette';

export const asapRegular = css`
  font-family: 'Asap', sans-serif;
  font-weight: 400;
`;

export const asapSemiBold = css`
  font-family: 'Asap', sans-serif;
  font-weight: 600;
`;

export const firaCodeRegular = css`
  font-family: 'Fira Code', monospace;
  font-weight: 400;
`;

const GlobalStyle = createGlobalStyle`
  ${reset}

  @import url('https://fonts.googleapis.com/css?family=Asap:400,600|Fira+Code:400&display=swap');

  *,
  *::before,
  *::after {
    box-sizing: border-box;
  }

  html {
    font-size: 16px;
  }

  body {
    ${asapRegular}
    font-size: 0.9rem;
    line-height: 1.2;
    background-color: ${NES_PALETTE.get(0x30)};
    color: ${NES_PALETTE.get(0x0f)};
  }

  *::selection {
    background-color: ${NES_PALETTE.get(0x16)};
    color: ${NES_PALETTE.get(0x30)};
  }

  a {
    color: ${NES_PALETTE.get(0x01)};

    &:hover {
      color: ${NES_PALETTE.get(0x12)};
    }

    &:active {
      color: ${NES_PALETTE.get(0x16)};
    }
  }

  strong,
  b {
    font-weight: 600;
  }

  em,
  i {
    font-style: italic;
  }

  pre,
  code {
    ${firaCodeRegular}
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h5 {
    ${asapSemiBold}
    margin-top: 0.2rem;
    margin-bottom: 0.4rem;
  }

  p,
  figure,
  pre {
    margin-bottom: 0.2rem;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.6rem;
  }

  h3 {
    font-size: 1.3rem;
  }

  h4 {
    font-size: 1rem;
  }

  h5 {
    font-size: 0.8rem;
  }

  h6 {
    font-size: 0.6rem;
  }

  ul {
    list-style: square;

    li {
      margin-left: 1.5em;
    }
  }

  hr {
    border: 0;
    border-top: 1px solid ${NES_PALETTE.get(0x10)};
  }
`;

export default GlobalStyle;
