import React from 'react';
import styled from 'styled-components';
import { Link } from 'gatsby';

import NES_PALETTE from '../lib/nesPalette';

import GithubCorner from './GithubCorner';

const Header = ({
  className,
  siteTitle = '',
  description = '',
  github = '',
}) => (
  <header className={className}>
    {github ? (
      <a href={github}>
        <GithubCorner
          backgroundFill={NES_PALETTE.get(0x30)}
          foregroundFill={NES_PALETTE.get(0x25)}
        />
      </a>
    ) : null}
    <h1>
      <Link to="/">{siteTitle}</Link>
    </h1>
    <p>{description}</p>
  </header>
);

export default styled(Header)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 200px;
  background-color: ${NES_PALETTE.get(0x21)};
  color: ${NES_PALETTE.get(0x30)};
  padding: 2rem 4rem;

  h1 {
    text-shadow: 0 2px 0 ${NES_PALETTE.get(0x0f)};

    > a {
      color: inherit;
      text-decoration: none;
    }
  }

  ${GithubCorner} {
    position: absolute;
    top: 0;
    right: 0;
  }
`;
